import { PropsWithChildren } from "react"
import { Typography } from "@rp/react/web/mui/core"

function WatchlistDataTableEmptyCell({ value, children }: PropsWithChildren<{ value?: unknown }>) {
  if (value === undefined) return <Typography sx={{ px: 1 }}>-</Typography>
  return children
}

export { WatchlistDataTableEmptyCell }
